.section_chat {
    background: #fff;
    width: 100%;
    border: 1px solid #e6ecf5;
    border-radius: .125rem;
    .section__container {
        padding: 0;
        .chat__header_part {
            margin: 0; } } }
.chat__header_part {
    padding: 2vw;
    display: flex;
    border-bottom: 1px solid #e6ecf5; }
.chat__header_part a {
    font-size: 1.2vw;
    color: #515365; }
.chat__header_part a:not(:last-child):after {
     content: "/";
     padding: 0.2vw; }
.chat__content {
    display: flex; }
.chat__content_part {
    margin-bottom: 0;
    display: flex; }
.chat__content__left_wrapper {
    width: 40%;
    height: 40vw;
    border-right: 1px solid #e6ecf5;
    overflow: auto;
    .chat__content__left {
        display: flex;
        flex-direction: column; } }
.chat__content__right {
    display: flex;
    position: relative;
    width: 60%;
    flex-direction: column; }
.person_image {
    width: 2.34vw;
    height: 2.34vw;
    background-color: #9275cc;
    border: 0.1vw solid #fff;
    border-radius: 50%;
    margin-left: 0;
    display: flex;
    justify-content: center;
    align-items: center; }
.chat_content_single {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    padding: 1.4vw 2vw;
    &.active {
        background-color: #f4f6f9; } }
.chat__left_image {
    display: flex; }
.chat__content_title {
    color: #515365;
    font-size: 1.2vw;
    padding-bottom: 0.3vw; }
.chat_right_logo {
    span {
        color: #eceff3; } }
.chat_content_text {
    color: #888da8;
    font-size: 1.2vw;
    line-height: 1.3;
    .images {
        margin: 1vw 0 2vw;
        display: flex;
        flex-direction: column;
        .images_title_list {
            font-size: 0.8vw;
            margin-top: 1vw;
            display: flex; }
        .images_list {
            display: flex;
            width: 100%;
            span {
                background-color: #9a9fbf;
                height: 4vw;
                width: 4vw;
                display: flex;
                flex-direction: row;
                float: left;
                margin-right: 1vw;
                border-radius: 0.3vw; } } } }
.chat_content_time {
    color: #888da8;
    font-size: 0.9vw;
    padding-top: 0.5vw;
    display: inline-block; }
.chat_person_image {
    width: 3vw;
    margin-right: 1.5vw; }
.chat_person_image_multiple {
    .person_image {
        height: 1.1vw;
        width: 1.1vw;
        padding: 0.4vw;
        float: left;
        color: #FFF;
        font-size: 0.5vw; } }
.chat_fixed_name {
    h4 {
        color: #515365;
        font-weight: bold; }
    span {
        font-size: 2vw;
        color: #888da8;
        line-height: 2vw; }
    top: 0;
    left: 0;
    right: 0;
    box-sizing: border-box;
    padding: 2vw;
    background-color: #FFF;
    position: absolute;
    z-index: 5;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid #e6ecf5; }
#usrform {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    width: 100%; }
#usrform {
    border-top: 1px solid #e6ecf5;
    textarea {
        border-top: 1px solid #e6ecf5;
        width: 100%;
        border: 0;
        padding: 2vw;
        font-size: 1.2vw;
        max-height: 10vw;
        color: #888da8;
        overflow: auto; } }
.submit_part {
    input {
        color: #FFF;
        background-color: #ff6c4b;
        border: 0;
        padding: 0.6vw 1vw;
        border-radius: 0.3vw;
        font-size: 1vw; } }
.submit_part {
    display: flex;
    align-items: center;
    padding: 1.4vw 2vw;
    justify-content: space-between;
    border-top: 1px solid #e6ecf5; }
.submit_part {
    .icon-list__element {
        margin-right: 1.5vw; } }
.chat_right_logo {
    .icon-list__element {
        i {
            font-size: 1.5vw; } } }
.submit_part {
    .icon-list__element {
        i {
            font-size: 1.5vw; } } }
#usrform {
    textarea::placeholder {
        color: #888da8; } }
.chat_listing {
    position: absolute;
    top: 6vw;
    left: 0;
    right: 0;
    height: 35vh;
    bottom: 6vw;
    overflow: auto; }
.chat_listing_single {
    padding: 1.5vw 2vw; }
.chat__content_header {
    .chat_content_time {
        font-size: 0.8vw; } }
.chat__content_header {
    .chat__content_title {
        padding-top: 0.5vw;
        padding-bottom: 0;
        font-size: 1.2vw;
        color: #515365;
        font-weight: bold; } }
.chat__content_header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    line-height: 1;
    padding-bottom: 1vw; }
.chat__content_wrapper {
    display: flex;
    flex-direction: column;
    width: 100%; }
